.header {
  position: fixed;
  top: 0px;
  left: 0;
  right: 0;
  z-index: 5;
  max-width: 1260px;
  padding: 20px 30px; 
  margin: 0 auto; 
  &__inner {
    background-color: $bg-purple;
    border-radius: 15px;
    padding: 10px 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 30px;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
  }
  &__logo {
    img {
      width: 40px;
      filter: invert(0.5);
      transition: all 0.4s;
      &:hover {
        filter: invert(1);
      }
    }
  }
}

.nav {
  margin: 0 auto;
  &__list {
    display: flex;
    align-items: center;
    gap: 30px;
    &-item {
      letter-spacing: 0.2em;
      transition: all 0.6s;
      border-bottom: 1px solid transparent;
      color: $fs-gray;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 0;
        height: 3px;
        background-color: $bg-gray;
        transition: width 0.5s;
      }
      &:hover::before {
        width: 100%;
      }
      &:hover {
        text-shadow: 0 0 5px $fs-gray, 0 0 25px $fs-gray, 0 0 50px $fs-gray,
          0 0 100px $fs-gray;
        color: $fs-white;
      }
    }
  }
}

